/**
 * TelegramProvider - –∫–æ–º–ø–æ–Ω–µ–Ω—Ç –¥–ª—è —É–ø—Ä–∞–≤–ª–µ–Ω–∏—è –∏–Ω—Ç–µ–≥—Ä–∞—Ü–∏–µ–π —Å Telegram Mini App
 */

import React, { createContext, useContext, useEffect, useState } from 'react';
import type { TelegramWebApp, TelegramUser } from '../types/telegram';

interface TelegramContextType {
  webApp: TelegramWebApp | null;
  user: TelegramUser | null;
  isInTelegram: boolean;
  platform: string;
  colorScheme: 'light' | 'dark';
}

const TelegramContext = createContext<TelegramContextType>({
  webApp: null,
  user: null,
  isInTelegram: false,
  platform: 'unknown',
  colorScheme: 'dark'
});

export const useTelegram = () => useContext(TelegramContext);

interface TelegramProviderProps {
  children: React.ReactNode;
}

export const TelegramProvider: React.FC<TelegramProviderProps> = ({ children }) => {
  const [webApp, setWebApp] = useState<TelegramWebApp | null>(null);
  const [user, setUser] = useState<TelegramUser | null>(null);
  const [isInTelegram, setIsInTelegram] = useState(false);
  const [platform, setPlatform] = useState('unknown');
  const [colorScheme, setColorScheme] = useState<'light' | 'dark'>('dark');

  useEffect(() => {
    if (typeof window !== 'undefined' && window.Telegram?.WebApp) {
      const tgWebApp = window.Telegram.WebApp as TelegramWebApp;
      
      // –ò–Ω–∏—Ü–∏–∞–ª–∏–∑–∞—Ü–∏—è WebApp
      tgWebApp.ready();
      tgWebApp.expand();
      
      setWebApp(tgWebApp);
      setIsInTelegram(true);
      setPlatform(tgWebApp.platform || 'unknown');
      setColorScheme(tgWebApp.colorScheme || 'dark');
      
      // –ü–æ–ª—É—á–µ–Ω–∏–µ –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏–∏ –æ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ
      if (tgWebApp.initDataUnsafe?.user) {
        setUser(tgWebApp.initDataUnsafe.user);
        console.log('Telegram user:', tgWebApp.initDataUnsafe.user);
      }
      
      // –ù–∞—Å—Ç—Ä–æ–π–∫–∞ –∏–Ω—Ç–µ—Ä—Ñ–µ–π—Å–∞
      tgWebApp.setHeaderColor('#000000');
      tgWebApp.setBackgroundColor('#000000');
      
      // –û—Ç–∫–ª—é—á–∞–µ–º —Å—Ç–∞–Ω–¥–∞—Ä—Ç–Ω—É—é –∫–Ω–æ–ø–∫—É –∑–∞–∫—Ä—ã—Ç–∏—è
      tgWebApp.disableClosingConfirmation();
      
      console.log('Telegram WebApp initialized successfully');

      // –ò–Ω–∏—Ü–∏–∞–ª–∏–∑–∞—Ü–∏—è
      tgWebApp.ready();
      tgWebApp.expand();

      console.log('Telegram WebApp Context initialized:', {
        platform: tgWebApp.platform,
        colorScheme: tgWebApp.colorScheme,
        user: tgWebApp.initDataUnsafe?.user,
        version: tgWebApp.version
      });
    }
  }, []);

  const contextValue: TelegramContextType = {
    webApp,
    user,
    isInTelegram,
    platform,
    colorScheme
  };

  return (
    <TelegramContext.Provider value={contextValue}>
      {children}
    </TelegramContext.Provider>
  );
};

export default TelegramProvider;
