/**
 * API –∫–ª–∏–µ–Ω—Ç –¥–ª—è –≤–∑–∞–∏–º–æ–¥–µ–π—Å—Ç–≤–∏—è —Å backend
 */

const API_BASE_URL = (import.meta as any).env?.VITE_API_URL || 'http://localhost:3001/api';

interface ApiResponse<T = any> {
  success?: boolean;
  data?: T;
  error?: string;
}

interface GameScoreData {
  score: number;
  level: number;
  timePlayed: number;
}

interface GameScoreResponse {
  success: boolean;
  experienceGained: number;
  voltsGained: number;
  levelUpReward: number;
  leveledUp: boolean;
  newLevel: number;
  levelProgress: {
    currentLevel: number;
    experienceInLevel: number;
    experienceToNextLevel: number;
    progressPercentage: number;
  };
  newEnergy: number;
  totalExperience: number;
  totalVolts: number;
}

interface UserStats {
  level: number;
  totalExperience: number;
  levelProgress: {
    currentLevel: number;
    experienceInLevel: number;
    experienceToNextLevel: number;
    progressPercentage: number;
  };
  totalVolts: number;
  premiumVolts: number;
  energy: number;
  maxEnergy: number;
  energyLastRefilled: string;
  totalSessions: number;
  bestScore: number;
  totalTimePlayed: number;
  recentSessions: Array<{
    id: string;
    score: number;
    level: number;
    experienceGained: number;
    voltsGained: number;
    timePlayed: number;
    playedAt: string;
  }>;
}

interface LeaderboardEntry {
  rank: number;
  id: string;
  username: string;
  level: number;
  totalExperience: number;
  totalVolts: number;
}

class ApiClient {
  private token: string | null = null;

  constructor() {
    // –ü–æ–ª—É—á–∞–µ–º —Ç–æ–∫–µ–Ω –∏–∑ localStorage –∏–ª–∏ Telegram WebApp
    this.token = localStorage.getItem('auth_token');
    this.initializeTelegramAuth();
  }

  private initializeTelegramAuth() {
    // –ò–Ω–∏—Ü–∏–∞–ª–∏–∑–∞—Ü–∏—è –∞—É—Ç–µ–Ω—Ç–∏—Ñ–∏–∫–∞—Ü–∏–∏ —á–µ—Ä–µ–∑ Telegram WebApp
    if (typeof window !== 'undefined' && window.Telegram?.WebApp) {
      const webApp = window.Telegram.WebApp;
      if (webApp.initDataUnsafe?.user) {
        // –ê–≤—Ç–æ–º–∞—Ç–∏—á–µ—Å–∫–∞—è –∞—É—Ç–µ–Ω—Ç–∏—Ñ–∏–∫–∞—Ü–∏—è —á–µ—Ä–µ–∑ Telegram
        this.authenticateWithTelegram(webApp.initData);
      }
    }
  }

  private async authenticateWithTelegram(initData: string): Promise<void> {
    try {
      const response = await fetch(`${API_BASE_URL}/auth/telegram`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ initData }),
      });

      const data = await response.json();
      
      if (data.token) {
        this.token = data.token;
        localStorage.setItem('auth_token', data.token);
      }
    } catch (error) {
      console.error('Telegram authentication failed:', error);
    }
  }

  private async makeRequest<T>(
    endpoint: string,
    options: RequestInit = {}
  ): Promise<ApiResponse<T>> {
    try {
      const headers: Record<string, string> = {
        'Content-Type': 'application/json',
        ...(options.headers as Record<string, string>),
      };

      if (this.token) {
        headers.Authorization = `Bearer ${this.token}`;
      }

      const response = await fetch(`${API_BASE_URL}${endpoint}`, {
        ...options,
        headers,
      });

      const data = await response.json();

      if (!response.ok) {
        return {
          error: data.error || `HTTP ${response.status}`,
        };
      }

      return {
        success: true,
        data,
      };
    } catch (error) {
      console.error('API request failed:', error);
      return {
        error: error instanceof Error ? error.message : 'Network error',
      };
    }
  }

  // === –ò–ì–†–û–í–´–ï –ú–ï–¢–û–î–´ ===

  /**
   * –û—Ç–ø—Ä–∞–≤–ª—è–µ—Ç —Ä–µ–∑—É–ª—å—Ç–∞—Ç—ã –∏–≥—Ä—ã –Ω–∞ —Å–µ—Ä–≤–µ—Ä
   */
  async submitScore(scoreData: GameScoreData): Promise<ApiResponse<GameScoreResponse>> {
    return this.makeRequest<GameScoreResponse>('/game/score', {
      method: 'POST',
      body: JSON.stringify(scoreData),
    });
  }

  /**
   * –ü–æ–ª—É—á–∞–µ—Ç —Å—Ç–∞—Ç–∏—Å—Ç–∏–∫—É –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è
   */
  async getUserStats(): Promise<ApiResponse<UserStats>> {
    return this.makeRequest<UserStats>('/game/stats');
  }

  /**
   * –ü–æ–ª—É—á–∞–µ—Ç —Ç–∞–±–ª–∏—Ü—É –ª–∏–¥–µ—Ä–æ–≤
   */
  async getLeaderboard(type: 'level' | 'volts' | 'experience' = 'level', limit = 10): Promise<ApiResponse<{ leaderboard: LeaderboardEntry[] }>> {
    return this.makeRequest<{ leaderboard: LeaderboardEntry[] }>(`/game/leaderboard?type=${type}&limit=${limit}`);
  }

  /**
   * –í–æ—Å—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞–µ—Ç —ç–Ω–µ—Ä–≥–∏—é
   */
  async refillEnergy(): Promise<ApiResponse<{
    success: boolean;
    energyRefilled: number;
    newEnergy: number;
    maxEnergy: number;
  }>> {
    return this.makeRequest('/game/refill-energy', {
      method: 'POST',
    });
  }

  // === –í–°–ü–û–ú–û–ì–ê–¢–ï–õ–¨–ù–´–ï –ú–ï–¢–û–î–´ ===

  /**
   * –ü—Ä–æ–≤–µ—Ä—è–µ—Ç, –∞—É—Ç–µ–Ω—Ç–∏—Ñ–∏—Ü–∏—Ä–æ–≤–∞–Ω –ª–∏ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å
   */
  isAuthenticated(): boolean {
    return !!this.token;
  }

  /**
   * –£—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞–µ—Ç —Ç–æ–∫–µ–Ω –∞—É—Ç–µ–Ω—Ç–∏—Ñ–∏–∫–∞—Ü–∏–∏
   */
  setToken(token: string): void {
    this.token = token;
    localStorage.setItem('auth_token', token);
  }

  /**
   * –û—á–∏—â–∞–µ—Ç —Ç–æ–∫–µ–Ω –∞—É—Ç–µ–Ω—Ç–∏—Ñ–∏–∫–∞—Ü–∏–∏
   */
  clearToken(): void {
    this.token = null;
    localStorage.removeItem('auth_token');
  }
}

// –≠–∫—Å–ø–æ—Ä—Ç–∏—Ä—É–µ–º –µ–¥–∏–Ω—Å—Ç–≤–µ–Ω–Ω—ã–π —ç–∫–∑–µ–º–ø–ª—è—Ä
export const apiClient = new ApiClient();
export default apiClient;
