import React from 'react';
import ReactDOM from 'react-dom/client';
import App from './App.tsx';
import './index.css';

// –ò–Ω–∏—Ü–∏–∞–ª–∏–∑–∞—Ü–∏—è Telegram Mini App
// –†–∞—Å—à–∏—Ä—è–µ–º WebApp –¥–æ –ø–æ–ª–Ω–æ–≥–æ —ç–∫—Ä–∞–Ω–∞
if (typeof window !== 'undefined' && window.Telegram?.WebApp) {
  const tg = window.Telegram.WebApp;
  
  // –û—Å–Ω–æ–≤–Ω–∞—è –Ω–∞—Å—Ç—Ä–æ–π–∫–∞
  tg.ready();
  tg.expand();
  
  // –ù–∞—Å—Ç—Ä–æ–π–∫–∞ —Ü–≤–µ—Ç–æ–≤–æ–π —Ç–µ–º—ã
  tg.setHeaderColor('#000000');
  tg.setBackgroundColor('#000000');
  
  // –û—Ç–∫–ª—é—á–∞–µ–º –∫–Ω–æ–ø–∫—É "–ù–∞–∑–∞–¥" (–±—É–¥–µ–º —É–ø—Ä–∞–≤–ª—è—Ç—å –Ω–∞–≤–∏–≥–∞—Ü–∏–µ–π —Å–∞–º–∏)
  tg.BackButton.hide();
  
  // –í–∫–ª—é—á–∞–µ–º —Ç–∞–∫—Ç–∏–ª—å–Ω—ã–µ —É–≤–µ–¥–æ–º–ª–µ–Ω–∏—è –µ—Å–ª–∏ –¥–æ—Å—Ç—É–ø–Ω—ã
  if (tg.HapticFeedback) {
    tg.HapticFeedback.selectionChanged();
  }
  
  console.log('Telegram WebApp initialized:', {
    version: tg.version,
    platform: tg.platform,
    colorScheme: tg.colorScheme,
    user: tg.initDataUnsafe?.user
  });
}

ReactDOM.createRoot(document.getElementById('root')!).render(
  <React.StrictMode>
    <App />
  </React.StrictMode>,
);
